<template>
  <div class="search-page">
    <section class="page-fixed">
      <search-bar
        @onSearch="search"
        class="search-bar-fixed"
      ></search-bar>
      <nav class="search-type-wrap search-type-fixed">
        <ul>
          <li
            v-for="item in searchType"
            :class="{active: item === currentType}"
            @click="typeClick(item)"
          >
            <a>{{item}}</a>
          </li>
        </ul>
      </nav>
      <select-bar
        :list="selectList"
        @onSelectChange="selectChange"
        class="select-bar-fixed"
      ></select-bar>
    </section>
    <section class="page-container">
      <div class="page-bar">
        医生
        <span class="right">筛选</span>
      </div>
      <div class="result-container">
        <doctor-cell
          v-for="(item, index) in results"
          :key="item.name"
          :cell-data="item"
          :is-last="index === (results.length - 1)"
        ></doctor-cell>
      </div>
    </section>
  </div>
</template>

<script type="text/Babel">
  import { mapActions, mapState } from 'vuex';
  import SearchBar from '../components/searchBar/searchBar';
  import SelectBar from '../components/selectBar/selectBar';
  import DoctorCell from '../components/doctorCell/doctorCell';

  export default {
    pageTitle: '搜索',
    components: {
      SearchBar,
      SelectBar,
      DoctorCell,
    },
    data() {
      return {
        currentType: '相关医生',
        searchType: ['百科咨询', '医患问答', '相关医生', '治疗药品'],
        selectList: [
          {
            k: 'area',
            v: '区域',
            vkey: '0',
          },
          {
            k: 'duty',
            v: '医生职称',
            vkey: '0',
          },
          {
            k: 'time',
            v: '预约时间',
            vkey: '0',
          },
        ],
      };
    },
    computed: {
      ...mapState({
        results: ({ Search }) => Search.results,
      }),
    },
    methods: {
      ...mapActions([
        'sendSearch',
      ]),
      search(str) {
        this.sendSearch({
          str,
          area: this.selectList[0].vkey,
          duty: this.selectList[1].vkey,
        });
        window.console.log(str);
      },
      selectChange(type, item) {
        const len = this.selectList.length;
        /* eslint-disable no-plusplus */
        for (let i = 0; i < len; i++) {
          const t = this.selectList[i];
          if (t.k === type) {
            if (type === 'time') {
              // type 为 time 时候, 返回 weeks
              t.vkey = item;
            } else {
              t.v = item.v;
              t.vkey = item.k;
            }
            break;
          }
        }
      },
      typeClick(item) {
        this.currentType = item;
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../assets/style/variables";

  .search-bar-fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 13;
  }

  .search-type-fixed {
    position: fixed;
    top: $searchBarHeight;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 13;
  }

  .select-bar-fixed {
    position: fixed;
    top: $searchBarHeight + $searchTypeHeight;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 12;
  }

  .search-page{
    padding-top: $searchBarHeight + $searchTypeHeight + $selectBarHeight + .3rem;

    .page-container {

      .page-bar {
        text-align: left;
        background-color: $whiteColor;
        font-size: $fontPoint6rem;
        color: $textGrayColor;
        border-top: 1px solid $borderColor;
        border-bottom: 1px solid $borderColor;
        height: 1.3rem;
        line-height: 1.3rem;
        padding: 0 .3rem;
        span {
          color: inherit;
        }
      }
    }
  }

  .search-type-wrap {
    overflow: hidden;
    zoom: 1;
    border-bottom: 1px solid $borderColor;

    ul,li {
      list-style: none;
    }
    li {
      font-size: $fontPoint7rem;
      float: left;
      display: inline-block;
      width: 25%;
      padding: .6rem 0;
      background-color: $whiteColor;
      line-height: 1em;

      a {
        display: inline-block;
        height: 1em;
        width: 100%;
        border-right: 1px solid $borderColor;
      }

      &:last-child {
        a {
          border-right: 0;
        }
      }

      &.active {
        position: relative;

        a {
          color: $blueColor;
        }
        &:after {
          content: "";
          display: inline-block;
          width: 100%;
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 2px;
          background-color: $blueColor;
        }
      }

    }
  }

  .result-container {
    border-bottom: 1px solid $borderColor;
  }
</style>



