<template>
  <section class="doctor-cell" :class="{'is-last': isLast}" >
    <div class="doctor-cell-inner ">
      <div class="flex">
        <img :src="cellData.avatar" :onerror="icon" class="avatar">
        <div class="flex-1">
          <div class="row-1">
            <span class="doctor-name">{{cellData.name}}</span>
            <span class="doctor-duty">{{cellData.duty}}</span>
            <span class="doctor-tag" v-if="cellData.isConsult">可咨询</span>
            <span class="doctor-num right">预约量：{{cellData.applyNum}}</span>
          </div>
          <div class="row-2">
            {{cellData.from}}
            {{cellData.focus}}
          </div>
          <div class="row-3">
            擅长领域: {{cellData.skill}}
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script type="text/Babel">
  export default {
    data() {
      return {
        /* eslint-disable */
        icon: `this.src="${require('../../assets/images/avatar1.png')}"`,
        /* eslint-enable */
      };
    },
    props: {
      /* eslint-disable */
      cellData: {
        default: {},
      },
      isLast: {
        default: false,
      },
      /* eslint-enable */
    },
    mounted() {

    },
    methods: {

    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .doctor-cell {
    padding: 0 0 0 .3rem;
    background-color: $whiteColor;
    text-align: left;

    &:active {
      opacity: .6;
    }

    &.is-last {
      .doctor-cell-inner {
        border-bottom: 0;
      }
    }

    .doctor-cell-inner {
      padding: .3rem .3rem .3rem 0;
      border-bottom: 1px solid $borderColor;

      .avatar {
        width: 2.85rem;
        height: 2.85rem;
        border-radius: 2px;
        margin-right: .3rem;
      }

      .row-1 {
        line-height: $fontPoint7rem;
      }

      .doctor-name {
        font-size: $fontPoint7rem;

      }

      .doctor-duty {
        font-size: $fontPoint6rem;
        color: $textDarkGrayColor;
        margin-left: .25rem;
      }

      .doctor-tag {
        vertical-align: middle;
        display: inline-block;
        font-size: .4rem;
        padding: .05rem .3rem;
        line-height: normal;
        background-color: $tagBgColor;
        color: rgb(113, 123, 132);
        border-radius: 2px;
        margin-left: .25rem;
      }

      .doctor-num {
        font-size: $fontPoint6rem;
        color: $textGrayColor;

      }

      .row-2 {
        height: 1.3rem;
        line-height: 1.3rem;
        font-size: $fontPoint6rem;
        color: $textDarkGrayColor;
      }

      .row-3 {
        line-height: $fontPoint7rem;
        font-size: $fontPoint6rem;
        color: $textGrayColor;
      }
    }
  }
</style>



