<template>
  <section class="box-container" >
    <p class="box-title">预约时间(可多选)</p>
    <table class="week-table">
      <thead>
        <tr>
          <th v-for="t in weeks" :key="t.text">{{t.text}}</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td v-for="t in weeks" :key="t.text" :class="{ active: t.isCheckedAM}" @click="itemClick('AM', t)">上午<div class="sel-mask"></div></td>
        </tr>
        <tr>
          <td v-for="t in weeks" :key="t.text" :class="{ active: t.isCheckedPM}" @click="itemClick('PM', t)">下午<div class="sel-mask"></div></td>
        </tr>
      </tbody>
    </table>
    <section class="btn-container flex">
      <button class="btn btn-default is-plain flex-1" @click="cancelClick">取消</button>
      <button class="btn btn-primary flex-1" style="margin-left: .8rem;" @click="confirmClick">确定</button>
    </section>
  </section>
</template>

<script type="text/Babel">
  export default {
    data() {
      return {
        weeks: [
          {
            text: '周一',
            isCheckedAM: false,
            isCheckedPM: false,
          },
          {
            text: '周二',
            isCheckedAM: false,
            isCheckedPM: false,
          },
          {
            text: '周三',
            isCheckedAM: false,
            isCheckedPM: false,
          },
          {
            text: '周四',
            isCheckedAM: false,
            isCheckedPM: false,
          },
          {
            text: '周五',
            isCheckedAM: false,
            isCheckedPM: false,
          },
        ],
      };
    },
    props: {
      /* eslint-disable */
      list: {
        type: Array,
        default: function () {
          return [];
        },
      },
      type: {
        default: ''
      },
      /* eslint-enable */
    },
    mounted() {

    },
    methods: {
      itemClick(type, t) {
        /* eslint no-param-reassign: ["error", { "props": false }] */
        if (type === 'AM') {
          t.isCheckedAM = !t.isCheckedAM;
        } else {
          t.isCheckedPM = !t.isCheckedPM;
        }
      },
      cancelClick() {
        this.$emit('onSelect', '');
      },
      confirmClick() {
        this.$emit('onSelect', this.type, this.weeks);
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .box-container {
    padding: .6rem;
    padding-bottom: 1rem;
    background-color: $grayColor;
    font-size: .6rem;

    .box-title {
      text-align: left;
    }

    .week-table{
      width: 100%;
      border-collapse: collapse;
      margin-top: .3rem;
      text-align: center;

      th {
        color: $textGrayColor;
        font-weight: normal;
      }

      td {
        border: 1px solid $whiteColor;

        .sel-mask {
          display: none;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border: 1px solid $blueColor;
          background-color: rgba(60, 135, 242, .2);

          &:before {
            content: "";
            display: inline-block;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 0;
            height: 0;
            border-bottom: 15px solid rgb(29, 117, 240);
            border-left: 15px solid transparent;
          }

          &:after {
            box-sizing: content-box;
            content: "";
            border: 1px solid #fff;
            border-left: 0;
            border-top: 0;
            height: 8px;
            right: 2px;
            position: absolute;
            bottom: 1px;
            width: 4px;
            transition: transform .15s cubic-bezier(.71,-.46,.88,.6) .05s;
            transform-origin: center;
            transform: rotate(45deg) scaleY(1);
          }
        }

        &.active {
          color: $blueColor;
          .sel-mask {
            display: block;
          }
        }
      }

      th, td {
        position: relative;
        padding: .3rem;
      }
    }
  }

  .btn-container {
    margin-top: 1rem;
  }
</style>



