<template>
  <section>
    <section class="select-container" ref="wrapper">
      <div class="select-item" v-for="item in list" :key="item.k">
        <div class="select-item-container" :class="{'selecting': (activeKey === item.k)}" @click="itemClick(item)">
          <div class="select-item-border">
            <span class="select-item-title">{{item.v}} </span>
            <svg data-v-6cc1fce6="" width="10" height="10" xmlns="http://www.w3.org/2000/svg" version="1.1" class="select-item-icon">
              <polygon data-v-6cc1fce6="" points="0,3 10,3 5,8"></polygon>
            </svg>
          </div>
        </div>
      </div>
    </section>
    <section >
      <transition name="fadeInDown" >
        <section class="select-box" v-show="activeKey === 'area'" :style="{ 'max-height': wrapperHeight + 'px', overflow: 'auto'}">
          <text-list
            :list="areaList"
            :type="'area'"
            @onSelect="onSelect"
          ></text-list>
        </section>
      </transition>
      <transition name="fadeInDown" >
        <section class="select-box" v-show="activeKey === 'duty'" :style="{ 'max-height': wrapperHeight + 'px', overflow: 'auto'}">
          <text-list
            :list="dutyList"
            :type="'duty'"
            @onSelect="onSelect"
          ></text-list>
        </section>
      </transition>
      <transition name="fadeInDown" >
        <section class="select-box" v-show="activeKey === 'time'" :style="{ 'max-height': wrapperHeight + 'px', overflow: 'auto'}">
          <week-box
            :list="[]"
            :type="'time'"
            @onSelect="onSelect"
          ></week-box>
        </section>
      </transition>
    </section>
    <transition name="fadeIn">
      <div class="bg-cover" v-show="activeKey.length > 0" @click="() => {this.activeKey = '';}"></div>
    </transition>
  </section>
</template>

<script type="text/Babel">
  import TextList from './textList';
  import WeekBox from './weekBox';

  export default {
    components: {
      TextList,
      WeekBox,
    },
    data() {
      return {
        wrapperHeight: 0,
        activeKey: '',
        areaList: [
          {
            k: '0',
            v: '不限区域',
          },
          {
            k: '1',
            v: '福田',
          },
          {
            k: '2',
            v: '南山',
          },
          {
            k: '3',
            v: '罗湖',
          },
          {
            k: '4',
            v: '宝安',
          },
          {
            k: '5',
            v: '龙华',
          },
          {
            k: '6',
            v: '光明新区',
          },
        ],
        dutyList: [
          {
            k: '0',
            v: '不限职责',
          },
          {
            k: '1',
            v: '主任医生',
          },
          {
            k: '2',
            v: '副主任医生',
          },
          {
            k: '3',
            v: '主治医师',
          },
        ],
      };
    },
    props: {
      list: {
        deep: true,
      },
    },
    mounted() {
      this.initSlideBox();
    },
    methods: {
      itemClick(item) {
        this.activeKey = this.activeKey === item.k ? '' : item.k;
        this.$emit('onItemClick', item);
      },
      initSlideBox() {
        this.wrapperHeight =
          document.documentElement.clientHeight -
          this.$refs.wrapper.getBoundingClientRect().top -
          this.$refs.wrapper.clientHeight;
      },
      onSelect(type, item) {
        this.activeKey = '';
        this.$emit('onSelectChange', type, item);
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .select-container {
    background-color: $grayColor;
    border-bottom: 1px solid $borderColor;
    width: 100%;
    height: 1.84rem;
    display: flex;
    box-sizing: border-box;
    position: relative;
    z-index: 14;

    .select-item {
      color: #444;
      height: $selectBarHeight;
      text-align: center;
      flex: 1;

      .select-item-border {
        border-right: 1px solid $borderColor;
        font-size: .6rem;
        span {
          line-height: inherit;
        }
      }
      &:last-child {
        .select-item-border {
          border-right: 0;
        }
      }

      .select-item-container {
        width: 100%;
        height: 100%;
        position: relative;
        box-sizing: border-box;
        padding-top: .48rem;

        .select-item-icon {
          vertical-align: middle;
          transition: all .3s;
        }

        &.selecting {
          .select-item-title {
            color: $blueColor;
          }
          .select-item-icon {
            fill: $blueColor;
            transform: rotate(180deg);
            vertical-align: middle;
          }
        }
      }
    }
  }
  .select-box {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 10;
  }

  .bg-cover {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: rgba(0,0,0,.3);
  }

</style>



