<template>
  <section class="area-container" >
    <div class="area-item" v-for="item in list" @click="itemClick(item)">
      {{item.v}}
    </div>
  </section>
</template>

<script type="text/Babel">
  export default {
    data() {
      return {
      };
    },
    props: {
      /* eslint-disable */
      list: {
        type: Array,
        default: function () {
          return [];
        },
      },
      type: {
        default: ''
      },
      /* eslint-enable */
    },
    mounted() {

    },
    methods: {
      itemClick(item) {
        this.$emit('onSelect', this.type, item);
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .area-container {
    background-color: $grayColor;
    font-size: $fontPoint6rem;

    .area-item {
      line-height: $selectBarHeight;
      text-align: center;
      border-bottom: 1px solid $borderColor;
    }
  }
</style>



