<template>
  <div class="searchbar">
    <form class="searchbar-inner">
      <i class="searchbar-icon"></i>
      <input type="search" name="search" placeholder="请输入内容" class="searchbar-input" v-model="searchStr">
      <a type="submit" class="searchbar-submit" @click="searchClick">搜索</a>
    </form>
  </div>
</template>

<script type="text/Babel">
  export default {
    data() {
      return {
        searchStr: '',
      };
    },
    mounted() {

    },
    methods: {
      searchClick() {
        this.$emit('onSearch', this.searchStr);
      },
    },
  };
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  @import "../../assets/style/variables";

  .searchbar {
    padding: .2rem;
    align-items: center;
    display: flex;
    font-size: $fontPoint7rem;
    background-color: $grayColor;

    .searchbar-inner {
      background-color: #fff;
      border-radius: 2px;
      flex: 1;
      height: 1.2rem;
      line-height: 1.2rem;
      padding: .2rem .3rem;
      align-items: center;
      display: flex;
    }

    .searchbar-icon {
      background: url("../../assets/images/search.png") 0 0 no-repeat;
      background-size: 100% 100%;
      display: inline-block;
      width: 1.3rem;
      height: 1rem;
      margin-right: .2rem;
    }

    .searchbar-input {
      border: 0;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      outline: 0;
      font-size: .6rem;
    }

    .searchbar-submit {
      color: $greenColor;
      display: inline-block;
      width: 3em;
      text-align: center;
      margin: 0 .1rem;
      &:active {
        opacity: .5;
      }
    }
  }
</style>



